import React, { Component } from "react";
import axios from "axios";
export class Personel extends Component {
    static displayName = Personel.name;

    constructor(props) {
        super(props);
        this.state = {
            bildirim: [
                {
                    baslik: "",
                    icerik: ""
                }
            ]
        };
        this.handleSubmit = this.handleSubmit.bind(this);

        // personal page not include the Layout component
        // so we need to add the Layout component to the page

    }


    componentDidMount() {
        this.setState.bind(this);
        this.handleSubmit();

    }

    handleSubmit = () => {

        var url = window.location.href; // url = "https://isper.azurewebsites.net/views/MTYyJjEw"
        var urlArray = url.split("/"); // urlArray = ["https:", "", "isper.azurewebsites.net", "view", "MTYyJjEw"]
        var hash = urlArray[urlArray.length - 1]; // hash = "MTYyJjEw"

        console.log(hash);

        // decoded hash bufffer base64 and set personelSicilNo and bildirimId

        // var decodedHash = Buffer.from(hash, 'base64');
        // var decodedHashArray = decodedHash.split("&"); // decodedHashArray = ["162", "10"]
        // this.setState.personelSicilNo = decodedHashArray[0]; // this.state.personelSicilNo = "162"
        // this.setState.bildirimId = decodedHashArray[1]; // this.state.bildirimId = "10"


        axios.get(`https://isper.azurewebsites.net/view/${hash}`)
            .then(res => {
                // obje olarak gelen response'u array'e çeviriyoruz
                const data = Object.values(res.data);
                this.setState({ bildirim: data });
                console.log("data", data + " Bildirim" + this.state.bildirim);
                console.log("Baslik", this.state.bildirim[1] + " Icerik" + this.state.bildirim[2]);
            })
    }


    render() {
        return (

            <div className="mt-5 d-flex justify-content-left"
                dangerouslySetInnerHTML={{
                    __html: this.state.bildirim[2]
                }}
            />

        );
    }
}

