import React, { Component } from 'react';
import { Collapse, Container, Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink } from 'reactstrap';
import { Link } from 'react-router-dom';
import "./Css/FetchData.css"
import logo from "../images/logo.png";
import whitehouse from "../images/whitehouse.png";
import bell from "../images/bell.png";
import redusers from "../images/redusers.png";
import nextarrow from "../images/nextarrow.png";
import media from "../images/media.png"

export class FetchData extends Component {
  static displayName = FetchData.name;

  constructor(props) {
    super(props);
    this.state = { users: [] };
  }


  useEffect = () => {
    fetch('https://isper.azurewebsites.net/getall')
      .then(response => response.json())
      .then(data => {
        console.log("use" + data);
        this.setState({ users: data });
      });
  }

  // get all data from api and set users state
  componentDidMount() {
    this.useEffect();
  }

  static renderForecastsTable(users) {
    return (
      <table aria-labelledby="tabelLabel">
        <thead>
          <tr>
            <th>Ad</th>
            <th>Soyad </th>
            <th>Telefon</th>
            <th>ProjeId</th>
            <th>Personel Sicil No</th>
            <th>Okundu Bilgisi</th>
            <th>Okunma Tarihi</th>
          </tr>
        </thead>
        <tbody>
          {users.map(user =>
            <tr key={user.bildirimId}>
              <td>{user.ad}</td>
              <td>{user.soyad}</td>
              <td>{user.telefon}</td>
              <td>{user.projeId}</td>
              <td>{user.personelSicilNo}</td>
              <td>{user.okundu ? "Okundu" : "Okunmadı"}</td>
              <td>{user.okunmaTarihi}</td>
            </tr>
          )}
        </tbody>
      </table>
    );
  }

  render() {

    return (
      <div className="FetchData">
        {/* <Navbar className="navbar-expand-sm navbar-toggleable-sm ng-white border-bottom box-shadow mb-3" light>
          <Container>
            <NavbarBrand tag={Link} to="/">isper</NavbarBrand>
            <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
            <Collapse className="d-sm-inline-flex flex-sm-row-reverse" isOpen={!this.state.collapsed} navbar>
              <ul className="navbar-nav flex-grow">
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/">Home</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/bildirimler">Bildirimler</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/kullanicilar">Kullanıcılar</NavLink>
                </NavItem>
              </ul>
            </Collapse>
          </Container>
        </Navbar> */}

        <div className="navbar">
          <img src={logo} alt="" className="logo" />
          <ul className="top">
            <a href="/" className="home"><img src={whitehouse} alt="" />Anasayfa</a>
            <a href="/bildirimler" className="notifications"><img src={bell} alt="" /> <span>Bildirimler</span></a>
            <a href="/kullanicilar" className="users"><img src={redusers} alt="" />Kullanıcılar</a>
            <a href="/media" className="media"><img src={media} alt="" />Medya</a>
            
          </ul>
        </div>

        <h1>Kullanıcılar</h1>

        <table aria-labelledby="tabelLabel">
          <thead>
            <tr>
              <th>Bildirim Id</th>
              <th>Ad</th>
              <th>Soyad </th>
              <th>Telefon</th>
              <th>ProjeId</th>
              <th>Personel Sicil No</th>
              <th>Okundu Bilgisi</th>
              <th>Okunma Tarihi</th>
            </tr>
          </thead>
          <tbody>
            {this.state.users.map(user =>
              <tr key={user.bildirimId}>
                <td id="bildirimId" onClick={
                  () => {
                    fetch(`https://isper.azurewebsites.net/getBildirim/${user.bildirimId}`)
                      .then(response => response.json())
                      .then(data => {
                        alert("Bildirim Id: " + data.bildirimId + "\n" + "Başlık: " + data.baslik + "\n" + "İçerik: " + data.icerik + "\n" + "Tarih: " + data.girisTarihi);
                      }
                      )
                  }

                }>{user.bildirimId}</td>
                <td>{user.ad}</td>
                <td>{user.soyad}</td>
                <td>{user.telefon}</td>
                <td>{user.projeId}</td>
                <td>{user.personelSicilNo}</td>
                <td>{user.okundu ? "Okundu" : "Okunmadı"}</td>
                <td>{user.okunmaTarihi}</td>
              </tr>
            )}
          </tbody>
        </table>

        <div className="pagenumber">
          <div className="box">
            <span>1</span></div>
          <button>Sonraki</button>
          <img src={nextarrow} alt="" />
        </div>
      </div>
    );
  }

  async populateWeatherData() {
    const response = await fetch('https://localhost:5001/getall');
    const data = await response.json();
    console.log(data);
    this.setState({ users: data, loading: false });
  }
}
