import React, { Component } from 'react';
import axios from 'axios';
import { Collapse, Container, Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink } from 'reactstrap';
import { Link } from 'react-router-dom';
import logo from "../images/logo.png";
import whitehouse from "../images/whitehouse.png";
import redbell from "../images/redbell.png";
import users from "../images/users.png";
import nextarrow from "../images/nextarrow.png";
import media from "../images/media.png";
import "./Css/Counter.css"
export class Counter extends Component {
  static displayName = Counter.name;

  constructor(props) {
    super(props);

    this.state = { bildirimler: [], loading: true };


  }

  componentDidMount() {
    this.useEffect();
  }

  useEffect = () => {
    axios("https://isper.azurewebsites.net/Bildirim/getall")
      .then(response => {
        console.log(response.data);
        this.setState({ bildirimler: response.data, loading: false });
      });
  }


  render() {
    return (
      <div className="Counter">

        {/* <Navbar className="navbar-expand-sm navbar-toggleable-sm ng-white border-bottom box-shadow mb-3" light>
          <Container>
            <NavbarBrand tag={Link} to="/">isper</NavbarBrand>
            <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
            <Collapse className="d-sm-inline-flex flex-sm-row-reverse" isOpen={!this.state.collapsed} navbar>
              <ul className="navbar-nav flex-grow">
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/">Home</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/bildirimler">Bildirimler</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/kullanicilar">Kullanıcılar</NavLink>
                </NavItem>
              </ul>
            </Collapse>
          </Container>
        </Navbar> */}

        <div className="navbar">
          <img src={logo} alt="" className="logo" />
          <ul className="top">
            <a href="/" className="home"><img src={whitehouse} alt="" />Anasayfa</a>
            <a href="/bildirimler" className="notifications"><img src={redbell} alt="" /> <span>Bildirimler</span></a>
            <a href="/kullanicilar" className="users"><img src={users} alt="" />Kullanıcılar</a>
            <a href="/media" className="media"><img src={media} alt="" />Medya</a>
          </ul>
        </div>

        <h1 id="tabelLabel" >Bildirimler</h1>
        <p>Bildirimlerinizi buradan görebilirsiniz.</p>



        <table aria-labelledby="tabelLabel">
          <thead>
            <tr>
              <th id="notifications">Bildirim ID</th>
              <th id="title">Başlık</th>
              <th id="text">İçerik</th>
              <th id="date">Gönderim Tarihi</th>
            </tr>
          </thead>
          <tbody>
            {this.state.bildirimler.map(bildirim =>
              <tr key={bildirim.bildirimId}>
                <td id="bildirim">{bildirim.bildirimId}</td>
                <td>{bildirim.baslik}</td>
                <td>{bildirim.icerik}</td>
                <td>{bildirim.girisTarihi}</td>
              </tr>
            )}
          </tbody>
        </table>

        <div className="pagenumber">
          <div className="box">
            <span>1</span></div>
          <button>Sonraki</button>
          <img src={nextarrow} alt="" />
        </div>







      </div>
    );
  }
}
