import React, { Component } from 'react';
import toast from 'react-hot-toast';
import "./Css/Home.css"
import { Collapse, Container, Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink } from 'reactstrap';
import { Link } from 'react-router-dom';
import logo from "../images/logo.png";
import home from "../images/house.png";
import bell from "../images/bell.png";
import dotbell from "../images/dotbell.png";
import users from "../images/users.png";
import media from "../images/media.png"
export class Home extends Component {
  static displayName = Home.name;

  constructor(props) {
    super(props);
    this.state = {
      baslik: '',
      icerik: ''
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  // craeate toaster success 
  success() {
    this.toastId = toast.success("Bildirim Kaydedildi", {
      autoClose: 5000,
      closeButton: false
    });
  }



  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value });

  }

  handleSubmit(e) {
    e.preventDefault();
    console.log(this.state);

    fetch('https://isper.azurewebsites.net/Bildirim/add', {
      method: 'POST',
      body: JSON.stringify(this.state),
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(data => {
        console.log('Success:', data);
        alert("Bildirim Kaydedildi");
        this.success();
        window.location.reload();
      })


  }

  render() {


    return (
      <div className="Home">
        {/* <Navbar className="navbar-expand-sm navbar-toggleable-sm ng-white border-bottom box-shadow mb-3" light>
            <NavbarBrand tag={Link} to="/">isper</NavbarBrand>
            <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
            <Collapse className="d-sm-inline-flex flex-sm-row-reverse " isOpen={!this.state.collapsed} navbar>
              <ul className="navbar-nav flex-grow">
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/">Anasayfa</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/bildirimler">Bildirimler</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink tag={Link} className="text-dark" to="/kullanicilar">Kullanıcılar</NavLink>
                </NavItem>
              </ul>
            </Collapse>
        </Navbar> */}

        <div className="navbar">
          <img src={logo} alt="" className="logo" />
          <ul className="top">
            <a href="/" className="home"><img src={home} alt="" />Anasayfa</a>
            <a href="/bildirimler" className="notifications"><img src={bell} alt="" /> <img id="dot" src={dotbell} alt="" /><span>Bildirimler</span></a>
            <a href="/kullanicilar" className="users"><img src={users} alt="" />Kullanıcılar</a>
            <a href="/media" className="media"><img src={media} alt="" />Media</a>

          </ul>
        </div>

        <div className="header">
          Anasayfa
        </div>

        <div className="box">

          <h2>Lütfen Bildirim Başlığınızı Giriniz</h2>
          <input type="text" name="baslik" onChange={this.handleChange} />
          <h4>Lütfen İçerik Metininizi Giriniz</h4>
          <textarea placeholder="Metin Yazınız" type="text" name="icerik" id="icerik" onChange={this.handleChange} />
          <button className="vazgec" onClick={() => window.location.reload()}> <span>Vazgeç</span></button>
          <button className="kaydet" onClick={this.handleSubmit}><span>Kaydet</span></button>
        </div>

      </div>
    );
  }
}

